<template>
  <div class="goodAttrAll" @click.self.stop="$emit('hideGoodAttrFun');pageInit()" @touchmove.prevent>
    <div class="shade2" @click.self.stop="$emit('hideGoodAttrFun');pageInit()"></div>
    <div class="all">
      <div class="content">
        <div class="goodTitle">
          <div class="close" @click="$emit('hideGoodAttrFun');pageInit()"></div>
          <div class="luntaiLogo"></div>
          <div class="goodInfo">
            <div class="goodName">{{dataModel[luntai].goodInfo}}</div>
            <div class="price" v-if="selectedGood.price==null">￥{{dataModel[luntai].priceSection}}</div>
            <div v-else class="price">￥{{selectedGood.price}}</div>
            <div class="selectTypes" v-if="selectedGood.goodModel==''">请选择型号</div>
            <div v-else class="selectTypes">已选择型号:{{selectedGood.goodModel}}</div>
          </div>
        </div>
        <div class="goodTypes">
            <div class="goodTypesTitle">型号</div>
            <div class="goodTypesList">
              <ul>
                <li v-for="(value,key) in dataModel[luntai].goodTypes" @click="selectGoodTypes(value,key)" :class="{'active':value.selected}">{{value.goodModel}}</li>
              </ul>
            </div>
          </div>
          <div class="selectNum">
            <div class="selectNumTitle">购买数量</div>
            <div class="selectNumCounter">
              <div @click="countSubtract()">-</div>
              <div>{{selectedGood.goodCount}}</div>
              <div @click="countAdd()">+</div>
            </div>
          </div>
      </div>
       <div class="buyBtn" @click="submitFun">立即购买</div>
      </div>
  </div>
</template>
<script>
export default {
  activated(){
    // this.$forceUpdate();
    console.log(this.dataModel[this.luntai])
  },
  watch: {
    // 观察用户选择轮胎的num值是否发生改变，如发生改变，执行该函数
    luntai: function(val) {
      this.selectedGood.goodName = this.dataModel[val].goodInfo;
      this.selectedGood.goodNum = val;
      this.selectedGood.componyName = this.dataModel[val].componyName;
    }
  },
  methods: {
    // 用户选择类型时执行的函数
    selectGoodTypes(value,key) {
      this.selectedGood.goodModel = value.goodModel;
      this.selectedGood.price = value.price;
      this.selectedGood.goodId = value.goodId;
      this.selectedGood.goodModelIndex = key;
      for (
        var i = 0;
        i < this.dataModel[this.selectedGood.goodNum].goodTypes.length;
        i++
      ) {
        this.dataModel[this.selectedGood.goodNum].goodTypes[i].selected = false;
      }
      value.selected = true;
    },
    // 关闭弹出框时，初始化函数
    pageInit() {
      // 取消class选中效果，清空已选择的商品及属性 这里用定时器设置了0.5秒的延迟
      setTimeout(() => {
        for (
          var i = 0;
          i < this.dataModel[this.selectedGood.goodNum].goodTypes.length;
          i++
        ) {
          this.dataModel[this.selectedGood.goodNum].goodTypes[
            i
          ].selected = false;
        }
        this.selectedGood = {
          goodModel: "",
          price: null,
          goodName: "德轮-12R22.5",
          goodCount: 1,
          goodNum: this.luntai,
          componyName: "",
          goodId: ""
        };
      }, 500);
    },
    // 点击减号
    countSubtract() {
      if (this.selectedGood.goodCount > 1) {
        this.selectedGood.goodCount--;
      }
    },
    // 点击加号
    countAdd() {
      if (this.selectedGood.goodCount < 98) {
        this.selectedGood.goodCount++;
      }
    },
    // 点击立即购买按钮
    submitFun() {
      if (this.selectedGood.goodModel == "") {
        alert("请选择商品");
      } else {
        for (
          var i = 0;
          i < this.dataModel[this.selectedGood.goodNum].goodTypes.length;
          i++
        ) {
          this.dataModel[this.selectedGood.goodNum].goodTypes[
            i
          ].selected = false;
        }
        this.$router.push({
          path: "SettleMentPage",
          query: {
            goodModel: this.selectedGood.goodModel,
            price: this.selectedGood.price,
            goodName: this.selectedGood.goodName,
            goodCount: this.selectedGood.goodCount,
            goodNum: this.selectedGood.goodNum,
            componyName: this.selectedGood.componyName,
            goodId: this.selectedGood.goodId,
            goodModelIndex:this.selectedGood.goodModelIndex
          }
        });
      }
    }
  },
  // 父组件给子组件传值(这里是轮胎的num值)
  props: ["luntai"],
  data() {
    return {
      // 已选择商品的属性
      selectedGood: {
        goodModel: "", // 型号
        price: null, // 价格（单价）
        goodName: "德轮-12R22.5", // 品牌
        goodCount: 1, // 选择数量
        goodNum: 0, // 货号(model中的数组下标)
        componyName: "德轮橡胶股份有限公司",
        goodId: "", // 货物的唯一Id
        goodModelIndex:null
      }
    };
  }
};
</script>
<style lang="scss" scoped>
@import "../../static/scss/rem.scss";
// 总体
.goodAttrAll {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  width: 100%;
  height: 100%;
}
// 遮罩
.shade2{
  background-color: rgba($color: #000000, $alpha: 0.5);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 888;
}
// 内容
.all {
  position: absolute;
  background-color:none;
  bottom: 0;
  width: 100%;
  z-index: 999;
}
// 内容上部分
.content {
  width: 100%;
  height: auto;
  background-color: white;

  box-sizing: border-box;
  padding: pr(20/2) pr(20/2) 0 pr(20/2);
}

.goodTitle {
  width: 100%;
  height: pr(240/2);
  position: relative;
  overflow: hidden;
  padding-bottom: pr(20);
  border-bottom: pr(1/2) solid #f5f5f5;
}
.close {
  width: pr(37/2);
  height: pr(37/2);
  float: right;
  background-image: url("../assets/closeBtn.png");
  background-size: 100% 100%;
}
.luntaiLogo {
  width: pr(230/2);
  height: pr(240/2);
  box-sizing: border-box;
  border: pr(1/2) solid #f5f5f5;
  float: left;
  background-image: url("../assets/luntaiLogo.png");
  background-size: 100% 100%;
}
.goodInfo {
  float: left;
  margin-left: pr(34/2);
}
.goodName {
  margin-top: pr(88/2);
  font-size: pr(32/2);
}
.price {
  margin-top: pr(10/2);
  font-size: pr(34/2);
  color: #f1235e;
}
.selectTypes {
  margin-top: pr(10/2);
  font-size: pr(28/2);
}
.goodTypesTitle {
  font-size: pr(28/2);
  padding: pr(32/2) 0;
}
.goodTypesList {
  padding-bottom: pr(10);
  border-bottom: pr(1/2) solid #f5f5f5;
  ul {
    overflow: hidden;
    li {
      float: left;
      list-style-type: none;
      padding: pr(18/2) pr(26/2);
      border-radius: pr(30);
      font-size: pr(24/2);
      margin-right: pr(20/2);
      margin-bottom: pr(20/2);
      background-color: #eeeeee;
      color: #333333;
      border: pr(1.5) solid #eeeeee;
      transition: all 0.5s;
    }
    .active {
      color: #f1245e;
      border: pr(1.5) solid #f1245e;
      background-color: white;
    }
  }
}
.selectNum {
  overflow: hidden;
  padding: pr(28/2) 0;
  font-size: pr(28/2);
  .selectNumTitle {
    float: left;
    font-size: pr(28/2);
    line-height: pr(60/2);
  }
  .selectNumCounter {
    float: right;
    > div {
      text-align: center;
      line-height: pr(60/2);
      float: left;
      margin-left: pr(2/2);
      width: pr(70/2);
      height: pr(60/2);
      background-color: #f5f5f5;
    }
    .deActive {
      background-color: #fbfbfb;
    }
  }
}
.buyBtn {
  font-size: pr(30/2);
  background-color: #f1245e;
  text-align: center;
  color: white;
  padding: pr(35/2) 0;
}
</style>



