<template>
  <div class="all">
      <div class="blue_bg">
      </div>
      <div class="allContent">
          <!-- <div class="title" @click="">结算</div> -->
          <!-- 选择服务点地址组件 -->
          <SelectPlace :goodNum="selectedGood.goodNum" :goodModelIndex="selectedGood.goodModelIndex"  @postPlace="getPlace"></SelectPlace>
          <!-- 货物详情组件 -->
          <GoodsSettleMentInfo :goodModel="selectedGood.goodModel" :price="selectedGood.price" :goodName="selectedGood.goodName" :goodCount="selectedGood.goodCount" :goodNum="selectedGood.goodNum" :componyName="selectedGood.componyName"></GoodsSettleMentInfo> 
          <!-- 发票组件 -->
          <InvoicePlugin @postInvoice="getInvoice"></InvoicePlugin>
          <div class="placeHolderDiv"></div>
      </div>
	  <!-- 提交组件 -->
      <SubmitSettleMent :goodModel="selectedGood.goodModel" :price="selectedGood.price" :goodName="selectedGood.goodName" :goodCount="selectedGood.goodCount" :goodNum="selectedGood.goodNum" :canSubmit="invoiceInfo.isNeedInvoice==false?true:invoiceInfo.checked==true?true:false" @submit="payModeShow=true" @backToIndex="backToIndex()"></SubmitSettleMent>
      <transition name="fade">
	  <!-- 选择支付方式组件 -->
      <payMode v-show="payModeShow" @close="payModeShow=false;" @alipayPay="submit('alipay')" @wechatPay="submit('wechat')"></payMode>
      </transition>
  </div>
</template>
<script>
import SelectPlace from "@/components/SelectPlace.vue";
import GoodsSettleMentInfo from "@/components/GoodsSettleMentInfo.vue";
import InvoicePlugin from "@/components/InvoicePlugin.vue";
import SubmitSettleMent from "@/components/SubmitSettleMent.vue";
import payMode from "@/components/payMode.vue";
export default {
  deactivated() {
    this.$destroy();
  },
  data() {
    return {
      // 已选中的货物的信息
      selectedGood: {
        goodModel: "", // 型号
        goodModelIndex: null,
        price: null, // 价格（单价）
        goodName: "", // 品牌
        goodCount: null, // 选择数量
        goodNum: null, // 货号(0是德轮，1是德冠，2是喜达通，3是银宁)
        selectCity: 0, // 选择的城市的数组下标(与model对应)
        selectServicePlace: 0, //选择的服务点数组下标(与model对应)
        componyName: "", // 公司名称
        goodId: "",
        serviceId: ""
      },
      // 发票信息
      invoiceInfo: {
        isNeedInvoice: false, // 是否需要开发票
        invoiceType: "normal", // 发票类型
        name: "", // 发票抬头名
        taxId: "", // 税号
        componyAddress: "", // 公司地址
        phoneNumber: "", // 电话号码
        bankName: "", // 开户银行
        bankId: "", // 银行账号
        checked: true // 该发票类型是否正确(由下游组件判断)
      },
      payModeShow: false // 控制支付方式组件是否显示
    };
  },
  components: {
    SelectPlace,
    GoodsSettleMentInfo,
    InvoicePlugin,
    SubmitSettleMent,
    payMode
  },
  created() {
    this.fetchData();
  },
  methods: {
    // 返回首页的点击方法
    backToIndex() {
      this.dataModel[this.selectedGood.goodNum].goodTypes[this.selectedGood.goodModelIndex].selected = true;
      this.$router.push({name:'IndexPage',params:{fresh:false}});
    },
    //   将URL中的参数保存
    fetchData() {
      this.selectedGood.goodModel = this.$route.query.goodModel;
      this.selectedGood.price = this.$route.query.price;
      this.selectedGood.goodName = this.$route.query.goodName;
      this.selectedGood.goodCount = this.$route.query.goodCount;
      this.selectedGood.goodNum = this.$route.query.goodNum;
      this.selectedGood.componyName = this.$route.query.componyName;
      this.selectedGood.goodId = this.$route.query.goodId;
      this.selectedGood.goodModelIndex = this.$route.query.goodModelIndex;
    },
    // 得到服务点地址组件的地址 并保存
    getPlace(params) {
      this.selectedGood.selectCity = params.selectCity;
      this.selectedGood.selectServicePlace = params.selectServicePlace;
      this.selectedGood.serviceId = this.dataModel[
        this.selectedGood.goodNum
      ].goodTypes[this.selectedGood.goodModelIndex].salePlace[
        params.selectCity
      ].servicePlace[params.selectServicePlace].serviceId;
      console.log(this.selectedGood.serviceId);
    },
    // 得到发票组件的发票信息 并保存
    getInvoice(params) {
      this.invoiceInfo.isNeedInvoice = params.isNeedInvoice;
      this.invoiceInfo.invoiceType = params.invoiceType;
      this.invoiceInfo.name = params.name;
      this.invoiceInfo.taxId = params.taxId;
      this.invoiceInfo.componyAddress = params.componyAddress;
      this.invoiceInfo.phoneNumber = params.phoneNumber;
      this.invoiceInfo.bankName = params.bankName;
      this.invoiceInfo.bankId = params.bankId;
      this.invoiceInfo.checked = params.checked;
    },
    // 通知native端支付
    submit(type) {
      console.log(
        JSON.stringify({
          // 支付方式
          PaymentChannel: type,
          // // 城市
          // city: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].city,
          // // 服务点
          // placeName: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].servicePlace[this.selectedGood.selectServicePlace].placeName,
          // // 联系人
          // contactName: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].servicePlace[this.selectedGood.selectServicePlace].contactName,
          // // 详细地址
          // detailPlace: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].servicePlace[this.selectedGood.selectServicePlace].detailPlace,
          // // 联系人电话
          // contactPhoneNumber: this.dataModel[this.selectedGood.goodNum]
          //   .salePlace[this.selectedGood.selectCity].servicePlace[
          //   this.selectedGood.selectServicePlace
          // ].phoneNumber,
          // // 轮胎型号
          // goodModel: this.selectedGood.goodModel,
          // 轮胎唯一ID
          GoodsId: this.selectedGood.goodId,
          // // 轮胎单价
          // price: this.selectedGood.price,
          // // 轮胎品牌
          // goodName: this.selectedGood.goodName,
          // 数量
          CountOfDeal: parseInt(this.selectedGood.goodCount),
          // 服务ID
          ServiceId: this.selectedGood.serviceId,
          // // 货号(0是德轮，1是德冠，2是喜达通，3是银宁)
          // goodNum: this.selectedGood.goodNum,
          // 是否开发票
          IsNeedInvoice: this.invoiceInfo.isNeedInvoice,
          // 发票类型
          InvoiceType: this.invoiceInfo.invoiceType == "normal" ? 1 : 2,
          // 发票名称
          InvoiceTitle: this.invoiceInfo.name,
          // 发票ID
          InvoiceTax: this.invoiceInfo.taxId,
          // 公司名称
          CompanyAddress: this.invoiceInfo.componyAddress,
          // 发票电话
          CompanyNumber: this.invoiceInfo.phoneNumber,
          // 银行名称
          BankName: this.invoiceInfo.bankName,
          // 银行ID
          BankAccount: this.invoiceInfo.bankId
        })
      );
      window.JS.getOrderInfo(
        JSON.stringify({
          // 支付方式
          PaymentChannel: type,
          // // 城市
          // city: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].city,
          // // 服务点
          // placeName: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].servicePlace[this.selectedGood.selectServicePlace].placeName,
          // // 联系人
          // contactName: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].servicePlace[this.selectedGood.selectServicePlace].contactName,
          // // 详细地址
          // detailPlace: this.dataModel[this.selectedGood.goodNum].salePlace[
          //   this.selectedGood.selectCity
          // ].servicePlace[this.selectedGood.selectServicePlace].detailPlace,
          // // 联系人电话
          // contactPhoneNumber: this.dataModel[this.selectedGood.goodNum]
          //   .salePlace[this.selectedGood.selectCity].servicePlace[
          //   this.selectedGood.selectServicePlace
          // ].phoneNumber,
          // // 轮胎型号
          // goodModel: this.selectedGood.goodModel,
          // 轮胎唯一ID
          GoodsId: this.selectedGood.goodId,
          // // 轮胎单价
          // price: this.selectedGood.price,
          // // 轮胎品牌
          // goodName: this.selectedGood.goodName,
          // 数量
          CountOfDeal: this.selectedGood.goodCount,
          // // 货号(0是德轮，1是德冠，2是喜达通，3是银宁)
          // goodNum: this.selectedGood.goodNum,
          // 是否开发票
          IsNeedInvoice: this.invoiceInfo.isNeedInvoice,
          // 服务ID
          ServiceId: this.selectedGood.serviceId,
          // 发票类型
          InvoiceType: this.invoiceInfo.invoiceType == "normal" ? 1 : 2,
          // 发票名称
          InvoiceTitle: this.invoiceInfo.name,
          // 发票ID
          InvoiceTax: this.invoiceInfo.taxId,
          // 公司名称
          CompanyAddress: this.invoiceInfo.componyAddress,
          // 发票电话
          CompanyNumber: this.invoiceInfo.phoneNumber,
          // 银行名称
          BankName: this.invoiceInfo.bankName,
          // 银行ID
          BankAccount: this.invoiceInfo.bankId
        })
      );
      //  支付时,将支付方式组件关掉
      this.payModeShow = false;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "./SettleMentPage.scss";
</style>



