<template>
    <div class="clickBtn">
        <div :class="{'active':!isActive}"></div>
        <div :class="{'active':isActive}"></div>
    </div>
</template>
<script>
export default {
  props: ["isActive"]
};
</script>
<style lang="scss" scoped>
@import "../../static/scss/rem.scss";
.clickBtn {
  box-sizing: border-box;
  width: pr(33/2);
  height: pr(33/2);
  position: relative;
  > div {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    transition: all 0.5s;
  }
  >div:nth-child(1){
      background-image: url("../assets/circleBtn.png");
      background-size: 100%;
  }
  >div:nth-child(2){
      background-image: url("../assets/circleBtn_active.png");
      background-size: 100%;
  }
  .active{
      opacity: 1;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



