<template>
    <div class="all">
        <div class="backToIndex" @click="backToIndex()">
            <img src="@/assets/backToIndexLogo.png" alt="">
        </div>
        <div class="rightBox">
            <span @click="submit()">提交订单</span>
        </div>
        <div class="leftBox">
            <!-- <span>已优惠￥99</span> -->
            <span>合计</span>
            <span>￥{{(price)*goodCount}}</span>
        </div>
        
    </div>
</template>
<script>
export default {
  props: [
    "goodModel",
    "price",
    "goodName",
    "goodCount",
    "goodNum",
    "canSubmit"
  ],
  methods: {
    submit() {
      if (this.canSubmit == true) {
        //   通知上游父组件 可以提交了
        this.$emit("submit");
      } else {
        alert("发票信息填写错误，请重新填写");
      }
    },
    backToIndex(){
        this.$emit("backToIndex");
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../../static/scss/rem.scss";
.all {
  position: fixed;
  bottom: 0;
  height: pr(100/2);
  width: 100%;
  box-sizing: border-box;
  border-top: pr(1) solid #d2d2d2;
  background-color: white;
  overflow: hidden;
  .leftBox {
    float: right;
    margin-right: pr(20/2);
    font-size: pr(28/2);
    line-height: pr(100/2);
    // > span:nth-child(1) {
    //   color: rgb(204, 204, 204);
    // }
    > span:nth-child(1) {
      color: black;
    }
    > span:nth-child(2) {
      color: #f1245e;
    }
  }
  .rightBox {
    width: pr(240/2);
    height: pr(100/2);
    line-height: pr(100/2);
    text-align: center;
    background-color: #f1245e;
    color: white;
    float: right;
    font-size: pr(32/2);
  }
  .backToIndex {
    //   background-color: #f1245e;
    float: left;
    width: pr(100/2);
    height: pr(100/2);
    text-align: center;
    line-height: pr(100/2);
    > img {
      width: pr(73/2);
      height: pr(69/2);
      vertical-align: middle;
    }
  }
}
</style>



