                        </div>
                        <!-- 轮胎2 -->
                        <div class="buyBtn2 buyBtn" @click="showGoodAttrFun(1)">
                        </div>
                        
                    </div>
                    <div class="content1">
                        <img src="@/assets/hongfa.png" alt="">
                        <a href="tel:18003499419" class="kefu2"></a>
                        <!-- 轮胎3 -->
                        <div class="buyBtn3 buyBtn" @click="showGoodAttrFun(2)">
                        </div>
                        <!-- 轮胎4 -->
                        <div class="buyBtn4 buyBtn" @click="showGoodAttrFun(2)">
                        </div>
                        <!-- 轮胎5 -->
                        <div class="buyBtn1 buyBtn" @click="showGoodAttrFun(3)">
                        </div>
                        <!-- 轮胎6 -->
                        <div class="buyBtn2 buyBtn" @click="showGoodAttrFun(3)">
                        </div>
                    </div>
                </div>
            </div>
            <!-- 轮胎结束 -->
            <!-- 尿素开始 -->
            <div class="niaosu" id="niaosu">
