            <!-- 底部占位div结束 -->
            <!-- 透明黑色遮罩 -->
            <!-- <transition name="fade">
                <div class="shade" v-show="showGoodAttr"></div>
            </transition> -->
            <!-- 透明黑色遮罩结束 -->
            <!-- 弹出框组件开始 -->
            <transition name="fade">
              <goodattr v-show="showGoodAttr" @hideGoodAttrFun="hideGoodAttrFun()" :luntai="luntaiId"></goodattr>
            </transition>
            <!-- 弹出框组件结束 -->
        </div>
        <!-- 内容结束 -->
    </div>
    <a href="" id="backToTop" @click.prevent="custormAnchor('banner')">
        <img src="@/assets/toTop3.png" alt="">
    </a>
  </div>
</template>
<script>
import goodattr from "../../components/SelectGoodsAttribute.vue";
export default {
  methods: {
    // 锚点方法 该方法为了解决 html锚点和vue的hash路由冲突
    custormAnchor(anchorName) {
      // 找到锚点
      let anchorElement = document.getElementById(anchorName);
      // 如果对应id的锚点存在，就跳转到锚点
      if (anchorElement) {
        anchorElement.scrollIntoView();
      }
    },
    // 显示商品弹出框
    showGoodAttrFun(a) {
      this.showGoodAttr = true;
      this.luntaiId = a;
    },
    // 隐藏商品弹出框
    hideGoodAttrFun() {
      this.showGoodAttr = false;
    },
    // 付款成功 路由跳转至付款成功页面(需要native端调用该方法)
    paySuccess() {
      this.$router.push({
        path: "PaySuccessPage"
      });
    },
    payFail(){
        setTimeout(function(){
            alert("支付失败!");
        },500)
    }
  },
  mounted() {
    // 页面挂载时禁用右键菜单，选中功能，复制功能(方法在util.js中)
    this.disableMenu();
    // 把付款成功方法 暴露给window对象 以便于native调用
    window.paySuccess = this.paySuccess;
    window.payFail = this.payFail;
  },
  components: {
    goodattr
  },
  data() {
    return {
      showGoodAttr: false,
      luntaiId: 0
    };
  },
  activated(){
      if(this.$route.params.fresh == true){
          window.location.reload();
      }
      else if(this.$route.params.fresh ==false){
          this.custormAnchor('luntai');
      }
  }
};
</script>
<style lang="scss" scoped>
@import "./IndexPage.scss";
</style>



