<template>
  <div class="all">
      <img src="@/assets/payFail.png" alt="">
      <img src="@/assets/backToIndex.png" alt="" @click="backToIndex">
  </div>
</template>
<script>
export default {
  methods:{
    //  路由跳转 返回商城首页
      backToIndex(){
          this.$router.push({path:'/'});
      }
  }
}
</script>
<style lang="scss" scoped>
@import "./PayFailPage.scss";

</style>



