<template>
  <div class="all">
      <img src="@/assets/paySuccess.png" alt="">
      <img src="@/assets/backToIndex.png" alt="" @click="backToIndex">
  </div>
</template>
<script>
export default {
  methods:{
    //  路由跳转 返回商城首页
      backToIndex(){
          this.$router.push({name:'IndexPage',params:{fresh:true}});
      }
  }
}
</script>
<style lang="scss" scoped>
@import "./PaySuccessPage.scss";

</style>



