<template>
    <div @touchmove.prevent>
        <div class="blackShade"></div>
        <div class="payBox">
            <div class="alipay" @click="alipayPay()">
                <div><img src="@/assets/alipay.png" alt=""></div>
                <div>支付宝支付</div>
            </div>
            <div class="wechat" @click="wechatPay()">
                <div><img src="@/assets/wechat.png" alt=""></div>
                <div>微信支付</div>
            </div>
            <div class="closeBtn" @click="close()"></div>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        // 关闭按钮
        close(){
            this.$emit("close");
        },
        // 点击支付宝支付
        alipayPay(){
            this.$emit("close");
            this.$emit("alipayPay");
        },
        // 点击微信支付
        wechatPay(){
            this.$emit("close");
            this.$emit("wechatPay");
        }
    }
}
</script>
<style lang="scss" scoped>
@import "../../static/scss/rem.scss";
.blackShade{
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.5;
    position: fixed;
    z-index: 99;
    transition: all 0.5s;
}
.payBox{
    position: fixed;
    width: pr(550/2);
    height: pr(340/2);
    background-color: white;
    left: pr(100/2);
    top: pr(500/2);
    z-index: 100;
    transition: all 0.5s;
    box-sizing: border-box;
    padding: pr(50/2) pr(30/2);
    font-size: pr(36/2);
    line-height: pr(100/2);
    .alipay,.wechat{
        box-sizing: border-box;
        overflow: hidden;
        height: pr(100/2);
        >div{
            float: left;
            height: 100%;
            text-align: center;
        }
        >div:nth-child(2){
            box-sizing: border-box;
            width: pr(380/2);
            border: pr(1) solid #dcdcdc;
            border-left: none;
        }
        img{
            width: pr(100/2);
            height: pr(100/2);
        }
    }
    .wechat{
        margin-top: pr(32/2);
    }
}
.closeBtn{
    position: absolute;
    width: pr(46/2);
    height: pr(46/2);
    right: pr(-46/4);
    top: pr(-46/4);
    border-radius: pr(46/2);
    background-color: white;
    background-image: url("../assets/closeX.png");
    background-size: 100% 100%;
}
.fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
    opacity: 0
}
</style>



