import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from '@/page/IndexPage/IndexPage'
import PaySuccessPage from '@/page/PaySuccessPage/PaySuccessPage'
import PayFailPage from '@/page/PayFailPage/PayFailPage'
import SettleMentPage from '@/page/SettleMentPage/SettleMentPage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'IndexPage',
      component: IndexPage
    },
    {
      path:'/SettleMentPage',
      name:'SettleMentPage',
      component:SettleMentPage
    },
    {
      path:'/PaySuccessPage',
      name:'PaySuccessPage',
      component:PaySuccessPage
    },
    {
      path:'/PayFailPage',
      name:'PayFailPage',
      component:PayFailPage
    }
  ]
})



