export default {
    install(Vue, options) {
        Vue.prototype.disableMenu = function () {
            /**
                * 禁用右键菜单
                */
            document.oncontextmenu = function () {
                event.returnValue = false;
            };
            /**
             * 禁用选中功能
             */
            document.onselectstart = function () {
                event.returnValue = false;
            };
            /**
             * 禁用复制功能
             */
            document.oncopy = function () {
                event.returnValue = false;
            };
        }
    }
}


