<template>
    <div class="all">
        <div class="businessName">
            <div>{{componyName}}</div>
        </div>
        <div class="goodsInfo">
            <div class="leftBox">
                <img src="@/assets/luntaiLogo.png" alt="">
            </div>
            <div class="centerBox">
                <p>{{goodName}}</p>
                <p>型号:{{goodModel}}</p>
                <p>数量:{{goodCount}}</p>
            </div>
            <div class="rightBox">
                <p>￥{{price}}</p>
            </div>
        </div>
        <!-- <div class="serviceCost">
            <span>服务费</span>
            <div>
                <span>￥100</span>
                <span>￥1</span>
            </div>
        </div> -->
        <div class="allCost">
            <div class="blackLine"></div>
            <div class="content">
                <!-- <span>已优惠￥99</span> -->
                <span>合计</span>
                <span>￥{{(price)*goodCount}}</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:["goodModel","price","goodName","goodCount","goodNum","componyName"],
};
</script>
<style lang="scss" scoped>
@import "../../static/scss/rem.scss";
.all {
  margin-top: pr(23/2);
  background-color: white;
}
.businessName {
  box-sizing: border-box;
  padding: pr(32/2) pr(20/2);
  > div {
    border-left: pr(6/2) solid #f1245e;
    font-size: pr(28/2);
    text-indent: pr(24/2);
  }
}

.goodsInfo {
  padding: pr(20/2);
  background-color: #fbfbfb;
  overflow: hidden;
  .leftBox {
    float: left;
    > img {
      width: pr(98/2);
      height: pr(98/2);
      border: pr(1) solid #f2f2f2;
    }
  }
  .centerBox {
    float: left;
    margin-left: pr(20/2);
    > p:nth-child(1) {
      font-size: pr(28/2);
      color: black;
    }
    > p:nth-child(2),
    > p:nth-child(3) {
      font-size: pr(24/2);
      color: #666666;
    }
  }
  .rightBox {
    float: right;
    color: #f1235e;
    font-size: pr(28/2);
  }
}
.installCost {
  padding: pr(28/2) pr(20/2);
  font-size: pr(28/2);
  overflow: hidden;
  > span:nth-child(2) {
    color: #f1235e;
    float: right;
  }
}
.serviceCost {
  padding: pr(28/2) pr(20/2);
  font-size: pr(28/2);
  overflow: hidden;
  > div {
    float: right;
    > span:nth-child(1) {
      color: rgb(204, 204, 204);
      text-decoration: line-through;
    }
    > span:nth-child(2) {
      color: #f1235e;
    }
  }
}
.allCost {
    overflow: hidden;
    padding: pr(28/2) pr(20/2) 0 pr(20/2);
  .blackLine {
    border-top: pr(1) solid #f2f2f2;
  }
  .content{
      float: right;
      font-size: pr(28/2);
      padding: pr(28/2) 0;
      // >span:nth-child(1){
      //     color: rgb(204, 204, 204);
      // }
      >span:nth-child(1){
          color: black;
      }
      >span:nth-child(2){
          color: #f1235e;
      }
  }
}
</style>



