<template>
  <div>
      <div class="Prompt">
        <div>
            请选择售后地址：
        </div>
      </div>
      <!-- <div class="cityBoxType1">
          <div class="cityType1"  v-for="(value,key) in dataModel[goodNum].goodTypes[goodModelIndex].salePlace" :class="{'cityType1_active':selectCity==key}" @click="cityClick(key)">{{value.city}}</div>
      </div> -->
      <div class="placeBox">
          <div class="checkBox" v-for="(value,key) in dataModel[goodNum].goodTypes[goodModelIndex].salePlace[selectCity].servicePlace" @click="ServicePlaceClick(key)"><span>{{value.placeName}}</span><clickBtn :isActive="key==selectServicePlace" @click.native="ServicePlaceClick(key)"></clickBtn></div>
      </div>
      <div class="detailPlaceBox">
          <div class="title">
              {{dataModel[goodNum].goodTypes[goodModelIndex].salePlace[selectCity].servicePlace[selectServicePlace].placeName}}
          </div>
          <div class="contact">
              <span>{{dataModel[goodNum].goodTypes[goodModelIndex].salePlace[selectCity].servicePlace[selectServicePlace].contactName}}</span>
              <span>{{dataModel[goodNum].goodTypes[goodModelIndex].salePlace[selectCity].servicePlace[selectServicePlace].phoneNumber}}</span>
          </div>
          <div class="detailPlace">
              {{dataModel[goodNum].goodTypes[goodModelIndex].salePlace[selectCity].servicePlace[selectServicePlace].detailPlace}}
          </div>
      </div>
      <div class="colorLine">
      </div>
  </div>
</template>
<script>
import clickBtn from "./clickBtn.vue";
export default {
  components: {
    clickBtn
  },
  data() {
    return {
      selectCity: 0, //选择的城市(model中的数组下标)
      selectServicePlace: 0 //选择的服务点(model中的数组下标)
    };
  },
  props: ["goodNum", "goodModelIndex"],
  methods: {
    // 点击城市
    cityClick(key) {
      this.selectCity = key;
      this.selectServicePlace = 0;
      this.changePlace();
    },
    // 点击服务点
    ServicePlaceClick(key) {
      this.selectServicePlace = key;
      this.changePlace();
    },
    // 当地点发生变换时
    changePlace() {
      // 向上游父组件传递当前选中的信息
      this.$emit("postPlace", {
        selectCity: this.selectCity,
        selectServicePlace: this.selectServicePlace
      });
    }
  },
  mounted() {
    this.selectServiceId = this;
    // 挂载时向上游父组件传递默认选中信息
    this.changePlace();
  }
};
</script>
<style lang="scss" scoped>
@import "../../static/scss/rem.scss";
.Prompt {
  overflow: hidden;
  > div {
    background-color: white;
    width: 100%;
    height: pr(70/2);
    font-size: pr(28/2);
    color: #282828;
    opacity: 1;
    line-height: pr(70/2);
    text-align: left;
    text-indent: pr(31/2);
    margin-right: pr(10/2);
    border-top-left-radius: pr(20/2);
    border-top-right-radius: pr(20/2);
    border-bottom: 0.05rem solid #f2f2f2;
  }
}
.cityBoxType1 {
  overflow: hidden;
  > div {
    background-color: white;
  }
}
.cityType1 {
  float: left;
  width: pr(230/2);
  height: pr(70/2);
  font-size: pr(24/2);
  color: #282828;
  opacity: 0.8;
  line-height: pr(70/2);
  text-align: center;
  margin-right: pr(10/2);
  border-top-left-radius: pr(20/2);
  border-top-right-radius: pr(20/2);
}
.cityType1_active {
  opacity: 1;
  color: #246b8f;
}
.cityType1:last-child {
  float: right;
  margin-right: 0;
}
.cityType1:first-child {
  float: left;
}
.placeBox {
  box-sizing: border-box;
  padding: 0 pr(19/2);
  background-color: white;
  .checkBox {
    box-sizing: border-box;
    padding: pr(32/2) pr(12/2);
    font-size: pr(28/2);
    border-bottom: pr(1) solid #f2f2f2;
    > div {
      float: right;
    }
  }
}
.detailPlaceBox {
  line-height: pr(48/2);
  font-size: pr(28/2);
  padding: pr(14/2) pr(19/2);
  background-color: white;
  color: #666666;
  > div {
    box-sizing: border-box;
    padding: 0 pr(12/2);
  }
  .title {
    color: black;
  }
  .contact {
    > span:nth-child(2) {
      float: right;
    }
  }
}
.colorLine {
  width: 100%;
  height: pr(11/2);
  background-image: url("../assets/colorLine.png");
  background-size: 100%;
}
</style>



