<template>
    <div class="all">
        <div class="isNeedInvoice">
            <span>是否开具发票</span>
            <div>
                <div class="checkBox" @click="isNeedInvoice=true;changeInvoice();"><clickBtn :isActive="isNeedInvoice"></clickBtn><span>是</span></div>
                <div class="checkBox" @click="isNeedInvoice=false;changeInvoice();"><clickBtn :isActive="!isNeedInvoice"></clickBtn><span>否</span></div>
            </div>
        </div>
        <div class="invoiceType" v-show="isNeedInvoice">
            <span>发票类型</span>
            <div>
                <div class="checkBox" @click="invoiceType='normal';changeInvoice();"><clickBtn :isActive="invoiceType=='normal'"></clickBtn><span>普通发票</span></div>
                <div class="checkBox" @click="invoiceType='major';changeInvoice();"><clickBtn :isActive="invoiceType=='major'"></clickBtn><span>专用发票</span></div>
            </div>
        </div>
        <div class="name" v-show="isNeedInvoice">
            <span>名&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;称</span>
            <input type="text" placeholder="请输入抬头名称或极速开发票六位(必填)" v-model="name" @input="changeInvoice()">
        </div>
        <div class="taxId" v-show="isNeedInvoice">
            <span>税&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号</span>
            <input type="text" placeholder="输入纳税人识别号或社会统一征信代码(必填)" v-model="taxId" @input="changeInvoice()">
        </div>
        <div class="componyAddress" v-show="isNeedInvoice">
            <span>企业地址</span>
            <input type="text" :placeholder="`请输入企业注册地址${placeholder}`" v-model="componyAddress" @input="changeInvoice()">
        </div>
        <div class="phoneNumber" v-show="isNeedInvoice">
            <span>电话号码</span>
            <input type="text" :placeholder="`请输入企业电话号码${placeholder}`" v-model="phoneNumber" @input="changeInvoice()">
        </div>
        <div class="bankName" v-show="isNeedInvoice">
            <span>开户银行</span>
            <input type="text" :placeholder="`请输入企业开户银行${placeholder}`" v-model="bankName" @input="changeInvoice()">
        </div>
        <div class="bankId" v-show="isNeedInvoice">
            <span>银行账号</span>
            <input type="text" :placeholder="`请输入企业银行账号${placeholder}`" v-model="bankId" @input="changeInvoice()">
        </div>
    </div>
</template>
<script>
import clickBtn from "./clickBtn.vue";
export default {
  data() {
    return {
      isNeedInvoice: false, // 是否需要发票
      invoiceType: "normal",  // 发票类型
      name: "", // 发票抬头名字
      taxId: "",  // 税号id
      componyAddress: "", // 公司地址
      phoneNumber: "",  // 公司电话号码
      bankName: "", // 开户行
      bankId: "", // 银行帐号
      checked: false  // 该发票格式是否正确(组件中判断)
    };
  },
  mounted() {
    // 组件挂载时，调用该方法
	  this.changeInvoice();
  },
  components: {
    clickBtn
  },
  computed: {
    // 计算属性 修改发票 当前是可填还是必填
    placeholder() {
      return this.invoiceType == "normal" ? "(可填)" : "(必填)";
    }
  },
  methods: {
    // 初始化发票(暂时没用到)
    initInvoiceInfo() {
      this.isNeedInvoice = false;
      this.invoiceType = "normal";
      this.name = "";
      this.taxId = "";
      this.componyAddress = "";
      this.phoneNumber = "";
      this.bankName = "";
      this.bankId = "";
      this.checked = false;
    },
    // 核心方法，判断当前的发票格式是否正确
    checkInvoice() {
      if (this.invoiceType == "normal") {
        if (this.name != "" && this.taxId != "") {
          this.checked = true;
        } else {
          this.checked = false;
        }
      } else {
        if (
          this.name != "" &&
          this.taxId != "" &&
          this.componyAddress != "" &&
          this.phoneNumber != "" &&
          this.bankName != "" &&
          this.bankId != ""
        ) {
          this.checked = true;
        } else {
          this.checked = false;
        }
      }
    },
    // 当input中的值发生改变时，调用该方法
    changeInvoice() {
      // 先检测发票格式是否正确
      this.checkInvoice();
      // 向上游父组件传值
      this.$emit("postInvoice", {
        isNeedInvoice: this.isNeedInvoice,
        invoiceType: this.invoiceType,
        name: this.name,
        taxId: this.taxId,
        componyAddress: this.componyAddress,
        phoneNumber: this.phoneNumber,
        bankName: this.bankName,
        bankId: this.bankId,
        checked: this.checked
      });
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../../static/scss/rem.scss";
input::-webkit-input-placeholder {
  color: rgb(204, 204, 204);
}
.all {
  padding: 0 pr(20/2) ;
  background-color: white;
  font-size: pr(28/2);
  line-height: pr(48/2);
  margin-top: pr(20/2);
  > div {
    margin: pr(30/2) 0;
  }
  input {
    width: pr(530/2);
    height: pr(58/2);
    margin-left: pr(10);
    box-sizing: border-box;
    border: pr(1) solid #dcdcdc;
    border-radius: pr(6);
    padding-left: pr(20/2);
  }
  .isNeedInvoice,
  .invoiceType {
    overflow: hidden;
    > div {
      float: right;
    }
    .checkBox {
      color: #666666;
      float: left;
      > div {
        float: left;
        margin-right: pr(8/2);
        margin-top: pr(15/4);
      }
    }
    .checkBox:nth-child(1) {
      margin-right: pr(60/2);
    }
  }
}
</style>



